@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Red Hat Display', sans-serif;
}

body {
  max-width: 1440px;
  margin: 0 auto;
}

.btn {
  padding: 16px 29px 16px 29px ;
  border-radius: 29px;
  border: none ;
  cursor: pointer;
  color: white;
  margin-right: 16px;
  font-weight: 900;
  font-size: 16px;
}

.btn-primary {
  background: #4096a9;
  border: 1px solid #4096a9;
}
.btn-secondary {
  background: #855fb1 ;
  border: 1px solid #855fb1 ;

}

.path {
  margin: 0 auto;
  /* margin-top: 110px; */
  margin-bottom: 64px;
  text-align: center;
} 
.path__content {
  display: flex;
  flex-direction: column;
}
.path .line {
  align-self: center;
  border: 1px solid #878790;
  opacity: 0.25;
  height: 84px;
}
.path .number {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  line-height: 56px;
  border: 1px solid #878790;
  align-self: center;
  font-weight: 900;
  font-size: 16px;
  color: #878790;
  background: white;
}
.no-bottom {
  position: relative;
  margin-bottom: -26px;
  z-index: 10;
}
footer {
    padding: 116px 104px ;
    background: linear-gradient(rgba(77, 150, 169, 0.80),rgba(77, 150, 169, 0.80)),url(/react-meeting-landing-page/static/media/image-footer.79d350b9.jpg) no-repeat center;
    background-size: cover;
}
.Footer_footer__container__3SIZo {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
}

.Footer_footer__container__3SIZo h2 {
    font-weight: 900;
    font-size: 40px;
    line-height: 44px;
    color: #fafafa;
}
.Footer_footer__container__3SIZo p {
    font-weight: 500;
    font-size: 18px;
    line-height: 26px;
    color: #fafafa;
}

@media (max-width: 768px) {
    footer {
        background: linear-gradient(rgba(77, 150, 169, 0.80),rgba(77, 150, 169, 0.80)),url(/react-meeting-landing-page/static/media/image-footer.b047b797.jpg) no-repeat center;
        background-size: cover;
    }
    .Footer_footer__container__3SIZo {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
        text-align: center;
    }
    footer {
        padding: 92px 155px 66px 155px;
    }
    .Footer_footer__container__3SIZo h2 {
        font-size: 40px;
        margin-bottom: 32px;
    }
    .Footer_footer__container__3SIZo p {
        font-size: 18px;
        margin-bottom: 40px;
    }
}

@media (max-width: 591px) {
    footer {
        width: 100%;
        padding: 92px 24px 72px 24px;
    }
    .Footer_footer__container__3SIZo h2 {
        font-size: 32px;
        margin-bottom: 24px;
    }
    .Footer_footer__container__3SIZo p {
        font-size: 18px;
        margin-bottom: 32px;
        
    }
}
